import React from 'react';
import {Card, CardActions, CardHeader, CardMedia, CardTitle, CardText} from 'material-ui/Card';
import FlatButton from 'material-ui/FlatButton';

import FontIcon from 'material-ui/FontIcon';
import IconButton from 'material-ui/IconButton';

 //Tweet{
  // in top avtar,from, id, time,
  // in middle tweet, photo(if there)
  // in bottom <comments, retweets, Likes, Direct Message>
//}

class Tweet extends React.Component{

  


    render()
    {

        const style={
            width:"60vw",
            //'margin-left':"20vw",
            //'margin-right':"20vh"
        }

        const stylet={
            color:'#5D6D7E'
        }

       return <Card style={style} align="left">
        <CardHeader margin="0" padding="0"
          title={this.props.from||'from'}
          subtitle={this.props.id||"id"}
          avatar={this.props.avatar||"ramu"}
          containerStyle={{
            'margin':"0vw",
            'padding':'0',
            width:'70vw',
            left:0
          }
          }
        />
      
        <CardTitle title={this.props.tweet||"tweet"} subtitle={this.props.time||"time"} />
        <CardActions>

        <IconButton
      iconClassName="muidocs-icon-custom-github" tooltip="bottom-right"
      tooltipPosition="bottom-right"
    />

          <FlatButton label={"comment "+this.props.comments} style={stylet}/>
          <FlatButton label={"Retweet "+this.props.retweets} style={stylet}/>
          <FlatButton label={"like "+this.props.love} style={stylet}/>
          <FlatButton label={"Direct Message "+this.props.dm} style={stylet}/>
        </CardActions>
      </Card>}
}

export default Tweet;


